<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <div style = "text-align:center;">
        <el-button @click.native.prevent="handleRegister">注册</el-button>
        <el-button type="primary" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      </div>
      <el-dialog title="注册" :visible.sync="ensure">
        <el-form-item prop="account">
          <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="请填入邮箱"/>
        </el-form-item>
        <el-form-item prop="checkPass">
          <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="请设置密码"/>
        </el-form-item>
        <div slot="footer" class="dialog-footer">
          <el-button @click="ensure = false">取 消</el-button>
          <el-button type="primary" @click="onSubmit" :loading="registering">确 定</el-button>
        </div>
      </el-dialog>
    </el-form-item>
  </el-form>
</template>

<script>
  import { requestLogin, userRegister } from '../api/api';
  //import NProgress from 'nprogress'
  export default {
    data() {
      return {
        ensure: false,
        registering: false,
        logining: false,
        ruleForm2: {
          account: '',
          checkPass: ''
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
      onSubmit() {
        var params = { username: this.ruleForm2.account, password: this.ruleForm2.checkPass };
        this.registering = true;
        userRegister(params).then(data => {
          this.registering = false;
          this.ensure = false;
          this.$message({message: '注册成功'});
        }).catch(error => {
          this.registering = false;
          var msg='注册失败';
          if(!error.hasOwnProperty('response')) msg=msg;
          else if(!error.response.hasOwnProperty('data')) msg=msg;
          else if(!error.response.data.hasOwnProperty('message')) msg=msg;
          else msg=msg+':'+error.response.data.message;
          this.$message({message: msg, type: 'error'});
        })
      },
      handleRegister() {
        this.ensure=true;
      },
      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      handleSubmit2(ev) {
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            this.logining = true;
            var loginParams = { username: this.ruleForm2.account, password: this.ruleForm2.checkPass };
            requestLogin(loginParams).then(data => {
              this.logining = false;
              console.log(JSON.stringify(data));
              sessionStorage.setItem('user', JSON.stringify(data));
              this.$router.push({ path: '/datasource' });
            }).catch(error => {
              this.logining = false;
              this.$message({
                message: '登陆失败,  用户名／密码错误', 
                cause: error.toString(),
                type: 'error'
              })
          })} else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


